
<template>
  <div :class="{show:detailShow}" class="detail">
    <div>
      <div @click="hide" @touchmove.stop.prevent class="details"></div>
      <div class="detail-content">
        <div class="detail-type-one">
            <span class="close" @click="hide"></span>
          <div @touchmove.stop class="detail-tips">
            <img :src="imgUrl" alt>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {},
 
  watch: {},
  data() {
    return {
      detailShow: false,
      imgUrl:'',
      imgSrc: {
        k1: require("../assets/images/newRetailPro/dialog/K1_1@2x.png"),
        m1t: require("../assets/images/newRetailPro/dialog/M1T_1@2x.png"),
        m1ts: require("../assets/images/newRetailPro/dialog/M1TS_1@2x.png"),
        a1: require("../assets/images/newRetailPro/dialog/A1_1@2x.png"),
        a2: require("../assets/images/newRetailPro/dialog/A2@2xd.png"),
        e148t: require("../assets/images/newRetailPro/dialog/E148T_1@2x.png"),
        e148w: require("../assets/images/newRetailPro/dialog/E148W_1@2x.png"),
        e148tw: require("../assets/images/newRetailPro/dialog/E148TW_1@2x.png"),
        e196twd: require("../assets/images/newRetailPro/dialog/E1 96TWD_1@2x.png"),
        e264tw: require("../assets/images/newRetailPro/dialog/E264Tw_1@2x.png"),
        e323tw: require("../assets/images/newRetailPro/dialog/E323TW_1@2x.png")
      }
    };
  },
   mounted() {
       this.imgUrl = this.imgSrc.k1;
   },
  methods: {
    show(key) {
        console.log(key)
      this.detailShow = true;
    //   console.log(this.imgSrc[key])
      this.imgUrl = this.imgSrc[key];
    },
    hide() {
      this.detailShow = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.detail {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  z-index: 9999;

  > div {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.2);
  }
}
.details {
  z-index: 10000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.detail.show {
  display: flex;
}

.detail-content {
  position: relative;
  z-index: 10001;
  height: 77.3%;
  width: 82%;
  background: #ffffff;
  border-radius: 4px;
  font-size: 0.36rem;
  animation-duration: 0.2s;
  animation-fill-mode: both;
  animation-name: zoomIn;
  animation-iteration-count: 1;
}
.detail-type-one {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.detail-title {
  // text-align: center;
  padding: 0.26rem 0;
  //   height: 7.3%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 0.02rem solid #e1e1e1;
}
.detail-tips {
  flex: 1;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding: 0.27rem 0.36rem;
  .tips-text {
    padding: 0.12rem 0.24rem;
    text-align: left;
    background: #f4f4f4;
    border-radius: 4px;
    font-size: 0.24rem;
    color: #333333;
    margin-bottom: 0.08rem;
  }
  img {
    margin-top: 0.2rem;
    width: 100%;
  }
}
.close{
    position: absolute;
    right: .25rem;
    top: .25rem;
    display: block;
    width: .5rem;
    height: .5rem;
    background-image: url('../assets/images/newRetailPro/dialog/off@2x.png');
    background-repeat: no-repeat;
    background-size: 100%;
}

// .detail-footer {
//   display: flex;
//   align-items: center;
//   justify-content: flex-end;
//   color: #1390f4;
//   padding: 0.25rem ;
//   // font-size: .36rem;
// }
@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
</style>



